.sidebar {
    background-color: var(--dark-gray);
    width: 250px;
    min-width: 250px;
    color: white;

    .sidebar-content {
        position: fixed;
        width: 250px;
        min-width: 250px;
        height: 100%;
        padding: 10px;
    }

    a {
        color: lightgray;
        text-decoration: none;
    }

    .search {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border: 1px solid lightgray;
        border-radius: 5px;
        padding: 10px; 
        color: lightgray;
        cursor: pointer;
    }
}